export function validateSignup(values) {
    let errors = {};

    if (!values?.fullName?.trim()) {
        errors.fullName = "  ";
    }
    if (!values?.phoneNumber) {
        errors.phoneNumber = "  ";
    }
    if (!values?.email?.trim()) {
        errors.email = "  ";
    }
    if (!values?.address1?.trim()) {
        errors.address1 = "  ";
    }
    if (!values?.address2?.trim()) {
        errors.address2 = "  ";
    }
    if (!values?.zipcode) {
        errors.zipcode = " ";
    }
    if (!values?.city?.trim()) {
        errors.city = " ";
    }

    return errors;
}
